/*optional for bringing icon in center*/

.ac-animated-svg-icon {
  /* left: 10%; */
  
  transform:  translateY(50%);

}
/*optional animation on hover toggle button */
.toggle-button {
  position: absolute;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  font-family: sans-serif;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  left: 50%;
  top: 5%;
  transform: translateX(-50%);
  cursor: pointer;
  transition: background-color 0.5s;
  z-index: 1;
}
.toggle-button:hover {
  background-color: rgba(255, 255, 255, 1);
}
.toggle-button.toggle-on {
  background-color: rgba(28, 186, 110, 0.5);
}
.toggle-button.toggle-on:hover {
  background-color: rgba(28, 186, 110, 1);
}
/*optional ends*/

.ac-animated-svg-icon {
    /*icon - size*/
    width: 300px;
    height: 300px;

    display: inline-block;
    position: relative;
    box-sizing: border-box;
    text-align: center;
    
    cursor: pointer;
}
.ac-animated-svg-icon .ac-animated-svg-icon-contents {
    position: absolute;
    /* icon - padding */
    left: 20px;
    top: 20px;
    right: 20px;
    bottom: 20px;
}
